import React, { Component } from 'react';
import './App.css';
import { Grid, Row, Col } from 'react-flexbox-grid';
import Header from './components/Header/Header';
import Content from './components/Content/Content';

class App extends Component {
  render() {
    return (
      <Grid fluid>
        <Header />
        <Content />
      </Grid>
    );
  }
}

export default App;



